const beforeEnterCartValidation = (to, from, next) => {
  if (!localStorage.getItem('store_user')) {
    router.replace('/iniciar')
  } else if (!localStorage.getItem('cubamax_cart')) {
    router.replace('/carrito')
  } else {
    next()
  }
}

const beforePay = (to, from, next) => {
  if (!localStorage.getItem('cubamax_cart') || !to.params.request) {
    if (localStorage.getItem('cubamax_cart') && localStorage.getItem('cubamax_cart').length > 0) {
      router.replace('/carrito')
    } else {
      router.replace('/')
    }
  } else {
    next()
  }
}

