<template>
  <div>
      <ValidationObserver ref="'observer'">

            <v-data-table
              :headers="headers"
              :items="products"
              hide-default-header
              hide-default-footer
              class="elevation-0"
            >

              <!-- Top row -->
              <template v-slot:top>
                <v-row align="center" justify="start">
                  <v-col md="4" sm="6" xs="12">
                    <!-- <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Buscar producto"
                      class="mx-4"
                      single-line
                      clearable
                      hide-details
                    ></v-text-field> -->
                    <h2 class=" text-bolder">{{ name }}</h2>
                  </v-col>
                </v-row>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-col align="center">
                  <v-btn class="text-bold close-btn" outlined color="secondary" @click="removeFromCart(item)">
                    <v-icon>mdi-close</v-icon> Eliminar
                  </v-btn>
                </v-col>
              </template>

              <template v-slot:item.product="{ item }">
                <v-col id="horizontal-card" align="center" class="px-0">
                  <v-row class="ma-0" align="center">
                    <v-col cols="5" align="center" class="pa-0" v-if="!breakpoint">
                      <v-img class="rounded-xl" height="154" max-height="154" width="200px" max-width="200px" :src="getImgUrl(item.image)" :lazy-src="getImgUrl(item.image)"></v-img>
                    </v-col>
                    <v-col id="info" align="center">
                        <p id="title" @click="goToDetails(item)">{{ item.name }}</p>
                        <p id="product-info">{{ item.description }}</p>
                    </v-col>
                  </v-row>
                </v-col>
              </template>

              <template v-slot:item.amount="{ item }">
                <v-col sm="12" class="px-0">
                  <p class="text-center">Cantidad</p>
                  <ValidationProvider v-slot="{ errors }" name="amount" rules="required|moreThanZero">
                    <v-text-field
                      id="amount"
                      v-model="item.amount"
                      type="number"
                      color="primary"
                      outlined
                      dense
                      min="1"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      @input="addToCart(item), calculateProductSubtotal()"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
              </template>

              <template v-slot:item.price="{ item }">
                <v-col class="px-0">
                  <p class="text-center">Precio x unidad</p>
                  <p class="text-center text-info text-bold">${{ Number.parseFloat(item.price).toFixed(2) }}</p>
                </v-col>
              </template>

              <template v-slot:item.subtotal="{ item }">
                <v-col class="pl-0">
                  <p class="text-center">Subtotal</p>
                  <p class="text-center text-info text-bold">${{ Number.parseFloat(item.subtotal).toFixed(2) }}</p>
                </v-col>
              </template>

              <template v-slot:item.divider="{ item }">
                <v-divider vertical inset class="row-divider"
                  :style="$vuetify.breakpoint.name === 'xs' ? 'display: none; max-height: 150px !important; height: 150px !important;' : 'max-height: 150px !important; height: 150px !important;'"
                >
                </v-divider>
              </template>

              <template v-slot:no-data>
                <span>No hay nada</span>
              </template>

              <template v-slot:no-results>
                <span>Sin resultados</span>
              </template>

            </v-data-table>

            <div id="to-pay-info" class="mt-5">
              <v-row align="center" justify="end">
                <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                  <span>Subtotal:</span>
                </v-col>
                <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                  <span class="mx-3">${{ subtotal }}</span>
                </v-col>
              </v-row>
              <v-row align="center" justify="end">
                <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                  <span>Precio de Envío:</span>
                </v-col>
                <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                  <span class="mx-3">Pendiente</span>
                </v-col>
              </v-row>
              <v-row align="center" justify="end">
                <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                  <span>Impuesto por la compra:</span>
                </v-col>
                <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                  <span class="mx-3">Pendiente</span>
                </v-col>
              </v-row>
              <v-row align="center" justify="end">
                <v-col cols="6">
                  <v-divider class="mt-3" inset></v-divider>
                </v-col>
              </v-row>
              <v-row align="center" justify="end">
                <v-col cols="8" md="5" sm="5" xs="8" class="text-end text-bold" style="font-family: 'Raleway-bold">
                  <span>Total</span>
                </v-col>
                <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                  <span class="mx-3">${{ subtotal }}</span>
                </v-col>
              </v-row>
            </div>

            <v-row class="my-16" justify="space-between" align="center">
              <v-col cols="12" :md="this.$vuetify.breakpoint.mobile ? 12 : 6">
                <span id="comment">*El precio final dependerá del costo del envío</span>
              </v-col>
              <v-col cols="12" :md="this.$vuetify.breakpoint.mobile ? 12 : 6">
                <v-row justify="end">
                  <v-btn class="text-bold close-btn mx-2" outlined color="secondary" to="/">
                    <v-icon class="mr-2">mdi-arrow-left</v-icon> Continuar comprando
                  </v-btn>
                  <v-btn :class="products.length > 0 ? 'text-bold mx-2 pr-3 close-btn' : 'text-bold mx-2 pr-3'"
                          color="primary"
                          :disabled="products.length === 0"
                          @click="goToConfirmRequest()">
                    Pagar <v-icon class="ml-2">mdi-arrow-right</v-icon>
                  </v-btn>
                </v-row>
              </v-col>
            </v-row>

        </ValidationObserver>
  </div>
</template>

<script>
import router from '../../../router'
import store from '../../../store'

export default {
  name: 'RestaurantOrder',
  props: {
    name: {
      type: String,
      required: true
    },
    products: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      subtotal: null
    }
  },
  created () {
    this.calculateProductSubtotal()
  },
  computed: {
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          document.querySelectorAll('.row-divider').forEach(x => {
            x.parentElement.setAttribute('style', 'display: none !important')
          })
          return true
        }
        case 'sm': {
          if (this.$vuetify.breakpoint.mobile) {
            document.querySelectorAll('.row-divider').forEach(x => {
              x.parentElement.parentElement.setAttribute('style', 'display: none !important')
            })
          }
          return true
        }
        case 'md': {
          // document.querySelector('.row-divider').parentElement.removeAttribute('style')
          return true
        }
        // case 'lg': return false
        // case 'xl': return false
      }
      return false
    },

    headers () {
      return [
        { text: '', align: 'center', value: 'actions' },
        { text: 'Producto', align: 'center', value: 'product', width: '75%' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Cantidad', align: 'center', value: 'amount' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Precio', align: 'center', value: 'price' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Subtotal', align: 'center', value: 'subtotal' }
      ]
    }
  },
  methods: {
    addToCart (product) {
      if (product.amount > 0) {
        if (store.getters.checkProductOnCart(product._id) < product.amount) {
          this.$store.dispatch('addToCart', product)
          this.$emit('rebuild')
        }
        if (store.getters.checkProductOnCart(product._id) > product.amount) {
          this.$store.dispatch('reduceAmountOfProductFromCart', product)
          this.$emit('rebuild')
        }
      }
      // product.amount = parseInt(product.amount) + parseInt(this.amount)
    },
    calculateProductSubtotal () {
      this.subtotal = 0
      this.products.forEach(p => {
        p.subtotal = p.price * p.amount
        this.subtotal += p.price * p.amount
      })
      this.subtotal = Number.parseFloat(this.subtotal).toFixed(2)
    },

    getImgUrl (image) {
      return process.env.VUE_APP_API_IMAGE_URL + image
    },

    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(product._id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
      this.removeFromCart(product)
    },

    removeFromCart (product) {
      this.$emit('removeFromCart', product)
      this.calculateProductSubtotal()
    },

    goToConfirmRequest () {
      // const ref = 'observer' + index
      // console.log(index, this.$refs)
      // this.$refs[ref].validate().then((result) => {
      //   if (result) {
      //     router.push({ path: '/orden/confirmar', params: { toBuy: this.stores[storeName] } })
      //   }
      // })
      router.push({ name: 'order-confirm', params: { toBuy: this.products } })
    }
  }
}
</script>

<style lang="scss" scoped>
.row.justify-center{
  font-weight: bold;
}
// p {
//   margin-bottom: 0;
// }
.v-image{
  box-shadow: 0px 0px 9px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 1px 0px 0px rgb(0 0 0 / 12%) !important;
}
.row{
  margin: 0;
}

.close-btn{
  box-shadow: 3px 1px 16px rgb(48 153 153 / 32%) !important;
  border: none;
}

.clear-btn{
  box-shadow: 16px 13px 32px #ff66003b !important;
  border: none;
}

#to-pay-info{
  .col{
    padding: 12px 0 0 0 !important;
  }
}
.text-info, .text-end{
  font-size: 18px;
}

#horizontal-card{

    #product-info{
      text-align: justify;
      font-family: Raleway;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      color: rgba(112,112,112,1);
      overflow-y: auto;
      max-height: 73%;
    }

    #title{
      font-size: 20px !important;
      line-height: normal;
      text-align: left;
      font-family: 'Raleway-bold' !important;
      color: rgba(112,112,112,1) !important;
    }

    #img-col{
      background: rgba(217,239,239,1);
      height: 260;
      width: inherit;
      border-top-left-radius: inherit;
      border-bottom-left-radius: inherit;
      border-top-right-radius: unset;

      .v-image{
        margin: 2.5rem 0 !important;
      }
    }

    .v-card__actions .col{
      padding: 0 12px !important;
    }

    .v-divider{
      margin: 9px 0;
    }

    .row{
      margin: 0;
    }

  .col {
    padding: 12px;
  }
}

#comment {
  // white-space: nowrap;
  text-align: left;
  font-family: Raleway;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #ff6600;
}
</style>
