import axiosCustom from '../axios'

export default class ContactsServices {
  static getUserContacts () {
    return axiosCustom.get('/user/contacts')
  }

  static getUserMostFrequentContact () {
    return axiosCustom.get('/user/contacts/frequent')
  }

  static createContact (data) {
    return axiosCustom.post('/user/contacts', data)
  }

  static updateContact (data) {
    return axiosCustom.patch(`/user/contacts/${data._id}`, data)
  }

  static deleteContact (data) {
    return axiosCustom.delete(`/user/contacts/${data._id}`, data)
  }
}
