<template>
  <v-main>
    <v-container id="cart-container" style="min-height: 76vh;">
      <v-row align="center">
        <h1 class="my-14 text-bolder">CARRITO DE COMPRAS</h1>
        <v-spacer></v-spacer>
        <v-btn :class="stores.length > 0 ? 'mx-2 text-bold clear-btn' : 'mx-2 text-bold'" elevation="0" color="error" :disabled="products.length == 0" @click="clearCart()">
          Vaciar carrito
          <v-icon class="ml-2">mdi-delete</v-icon>
        </v-btn>
      </v-row>

      <div v-for="(name, i) in storeNames" :key="i" class="mb-8 px-3">
        <RestaurantOrder :name="name" :products="stores[name]" @removeFromCart="removeFromCart($event)" @rebuild="rebuild()" />
      </div>

    </v-container>
  </v-main>
</template>

<script>
import RestaurantOrder from './RestaurantOrder'
import router from '../../../router'

export default {
  name: 'Cart',
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      search: null,
      products: this.$store.getters.cart(),
      stores: [],
      storeNames: [],
      pageProducts: []
    }
  },
  components: {
    RestaurantOrder
  },
  created () {
    this.buildCartList()
  },
  watch: {
    // products () {
    //   this.buildCartList()
    // }
  },
  // computed: {
  //   breakpoint () {
  //     switch (this.$vuetify.breakpoint.name) {
  //       case 'xs': {
  //         document.querySelectorAll('.row-divider').forEach(x => {
  //           x.parentElement.setAttribute('style', 'display: none')
  //         })
  //         return true
  //       }
  //       case 'sm': {
  //         // document.querySelector('.row-divider').parentElement.removeAttribute('hidden')
  //         return true
  //       }
  //       case 'md': {
  //         // document.querySelector('.row-divider').parentElement.removeAttribute('hidden')
  //         return true
  //       }
  //       // case 'lg': return false
  //       // case 'xl': return false
  //     }
  //     return false
  //   },

  //   headers () {
  //     return [
  //       { text: '', align: 'center', value: 'actions' },
  //       { text: 'Producto', align: 'center', value: 'product', width: '75%' },
  //       { text: '', align: 'center', value: 'divider' },
  //       { text: 'Cantidad', align: 'center', value: 'amount' },
  //       { text: '', align: 'center', value: 'divider' },
  //       { text: 'Precio', align: 'center', value: 'price' },
  //       { text: '', align: 'center', value: 'divider' },
  //       { text: 'Subtotal', align: 'center', value: 'subtotal' }
  //     ]
  //   }
  // },
  methods: {
    buildCartList () {
      this.products.forEach(item => {
        this.stores[item.store.name] = this.stores[item.store.name] || []
        var index = this.stores[item.store.name].find(p => {
          return p._id === item._id
        })
        index = this.stores[item.store.name].indexOf(index)
        if (index === -1) {
          item.amount = 1
          this.stores[item.store.name].push(item)
        } else {
          this.stores[item.store.name][index].amount = this.stores[item.store.name][index].amount + 1
        }
      })

      for (const key in this.stores) {
        this.storeNames.push(key)
      }
    },

    rebuild () {
      this.products = this.$store.getters.cart()
      for (const key in this.stores) {
        this.stores[key] = []
      }
      this.storeNames = []
      this.buildCartList()
    },

    customizedCartFilter (value, search, item) {
      search = search.toLocaleLowerCase()
      return value != null && (item.name.toString().toLocaleLowerCase().indexOf(search) !== -1 ||
              item.description.toString().toLocaleLowerCase().indexOf(search) !== -1 ||
              item.price.toString().toLocaleLowerCase().indexOf(search) !== -1 ||
              item.amount.toString().toLocaleLowerCase().indexOf(search) !== -1)
    },

    getImgUrl (image) {
      // var images = require.context('../../../assets', false)
      // return images('./' + image)
      return process.env.VUE_APP_API_IMAGE_URL + image
    },

    clearCart () {
      this.$store.dispatch('clearCart')
      this.products = this.$store.getters.cart()
      this.stores = []
      this.storeNames = []
    },

    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(product._id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
      this.removeFromCart(product)
    },

    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
      this.stores = []
      this.storeNames = []
      this.buildCartList()
    },

    goToConfirmRequest (index, storeName) {
      // const ref = 'observer' + index
      // console.log(index, this.$refs)
      // this.$refs[ref].validate().then((result) => {
      //   if (result) {
      //     router.push({ path: '/orden/confirmar', params: { toBuy: this.stores[storeName] } })
      //   }
      // })
      router.push({ name: 'order-confirm', params: { toBuy: this.stores[storeName] } })
    }
  }
}
</script>

<style lang="scss" scoped>
.row.justify-center{
  font-weight: bold;
}
// p {
//   margin-bottom: 0;
// }
.v-image{
  box-shadow: 0px 0px 9px -1px rgb(0 0 0 / 20%), 0px 0px 0px 0px rgb(0 0 0 / 14%), 0px 1px 0px 0px rgb(0 0 0 / 12%) !important;
}
.row{
  margin: 0;
}

.close-btn{
  box-shadow: 3px 1px 16px rgb(48 153 153 / 32%) !important;
  border: none;
}

.clear-btn{
  box-shadow: 16px 13px 32px #ff66003b !important;
  border: none;
}

#to-pay-info{
  .col{
    padding: 12px 0 0 0 !important;
  }
}
.text-info, .text-end{
  font-size: 18px;
}

#horizontal-card{

    #product-info{
      text-align: justify;
      font-family: Raleway;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      color: rgba(112,112,112,1);
      overflow-y: auto;
      max-height: 73%;
    }

    #title{
      font-size: 20px !important;
      line-height: normal;
      text-align: left;
      font-family: 'Raleway-bold' !important;
      color: rgba(112,112,112,1) !important;
    }

    #img-col{
      background: rgba(217,239,239,1);
      height: 260;
      width: inherit;
      border-top-left-radius: inherit;
      border-bottom-left-radius: inherit;
      border-top-right-radius: unset;

      .v-image{
        margin: 2.5rem 0 !important;
      }
    }

    .v-card__actions .col{
      padding: 0 12px !important;
    }

    .v-divider{
      margin: 9px 0;
    }

    .row{
      margin: 0;
    }

  .col {
    padding: 12px;
  }
}

#comment {
  white-space: nowrap;
  text-align: left;
  font-family: Raleway;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #ff6600;
}
</style>
