<template>
  <v-main>
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
      scroll-target="#scrolling-techniques-7"
      class="header-toolbar"
      style="position: fixed; z-index: 8; width: 100%;"
    >
      <back-button />

      <v-toolbar-title>Lista de ventas</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon class="mr-2">
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
    </v-app-bar>
    <v-container style="margin-top: 5.5rem">

        <v-card class="card-template">
          <v-row>
            <v-col class="img-col" cols="4" align="center" align-self="center">
              <v-img contain src="../../assets/casa-en-venta.png" lazy-src="../../assets/casa-en-venta.png"></v-img>
              <v-rating
                background-color="grey"
                color="warning"
                hover
                length="5"
                size="25"
                :value="4.5"
                class="mt-md-2 mt-sm-2 mb-4 text-md-left text-sm-left"
              ></v-rating>

              <v-chip color="green" pill outlined small>Abierto</v-chip>
              <!-- <v-chip color="red" pill outlined small>Cerrado</v-chip> -->

            </v-col>
            <v-col cols="8" class="px-0 pe-3 ps-md-3 px-sm-3 d-flex flex-column">
              <v-row>
                <v-col class="pa-0">
                  <h2 class="text-bold">Nombre de la venta</h2>
                  <p :style="{'fontSize': properties.fontSize}"><v-icon class="mr-1" :size="properties.iconSize">mdi-phone</v-icon><span class="text-bold"></span> 78565658</p>
                  <p :style="{'fontSize': properties.fontSize}"><v-icon class="mr-1" :size="properties.iconSize">mdi-map-marker</v-icon><span class="text-bold"></span> Calle 37 entre 30 y 34 Playa, La Habana</p>
                </v-col>
              </v-row>
              <v-row justify="end" class="f-10 align-end">
                <v-btn class="mr-2 text-capitalize" outlined rounded small text to="/venta/venta-de-yazmin">Mapa</v-btn>
                <v-btn class="text-capitalize" outlined rounded small text to="/venta/venta-de-yazmin">Ver venta</v-btn>
              </v-row>
            </v-col>
          </v-row>
        </v-card>

    </v-container>
  </v-main>
</template>

<script>
// import MenuStore from '@/components/MenuStore'
// import Product from '@/components/Product'
// import ProductsList from '@/components/ProductsList'
// import MostUsedServices from '@/components/MostUsedServices'
// import BestNotices from '@/components/BestNotices.vue'
import saleServices from '@/axios/store.services'
import BackButton from '../../components/BackButton.vue'

export default {
  name: 'Store',
  components: {
    BackButton
    // MenuStore,
    // Product
    // ProductsList
    // MostUsedServices,
    // BestNotices
  },
  data () {
    return {
      drawer: false,
      search: '',
      searching: false,
      department: null,
      imageSelected: null,
      categorySelected: null,
      restaurantSlug: this.$route.params.slug,
      hash: null,
      categorySlugPath: this.$route.params.category_slug || null,
      productSlugPath: this.$route.params.product_slug || null,

      options: {
        duration: 750,
        offset: 150,
        easing: 'easeInOutCubic'
      },
      markPosition: false,
      loading1: false,
      loading2: false,
      product: {
        images: [1, 2, 3, 4, 5]
      },
      products: this.$store.getters.products(),
      restaurantTemp: [],
      selectedItem: 1,
      categories: [
        { name: 'Todas las categorias', icon: 'mdi-account', _id: null, slug: 'todas-las-categorias' },
        { name: 'Accesorios', icon: 'mdi-account', _id: 1, slug: 'accesorios' },
        { name: 'Artesania', icon: 'mdi-account', _id: 2, slug: 'artesania' },
        { name: 'Decoracion', icon: 'mdi-flag', _id: 3, slug: 'decoracion' },
        { name: 'Del Hogar', icon: 'mdi-flag', _id: 4, slug: 'del-hogar' },
        { name: 'Ropa', icon: 'mdi-clock', _id: 5, slug: 'ropa' },
        { name: 'Tecnologia', icon: 'mdi-flag', _id: 6, slug: 'tecnologia' }
      ],
      properties: {
        mainImageWidth: 450,
        mainImageHeight: 398,
        bottomBarHeight: '245px',
        iconSize: 21,
        fontSize: '16px'
      }
    }
  },
  watch: {
    '$route' (to, from) {
      console.log(to)
    },
    '$vuetify.breakpoint.name' (name) {
      if (name === 'xs') {
        this.properties.bottomBarHeight = '245px'
        this.properties.mainImageHeight = '275px'
        this.properties.iconSize = 17
        this.properties.fontSize = '13px'
        return true
      }
      if (name === 'sm') {
        this.properties.bottomBarHeight = '315px'
        this.properties.iconSize = 17
        this.properties.fontSize = '13px'
      }
      if (name === 'md') {
        this.properties.bottomBarHeight = '382px'
        this.properties.iconSize = 21
        this.properties.fontSize = '16px'
      }
      if (name === 'lg') {
        this.properties.bottomBarHeight = '382px'
        this.properties.iconSize = 21
        this.properties.fontSize = '16px'
      }
      if (name === 'xl') {
        this.properties.bottomBarHeight = '382px'
        this.properties.iconSize = 21
        this.properties.fontSize = '16px'
      }
    }
  },
  created () {
    this.loading1 = true
    // loading restaurant information
    saleServices.getStores({ params: { slug: this.restaurantSlug } }).then((result) => {
      this.restaurant = result.data[0]
      this.loading1 = false
    }).catch((err) => {
      console.log(err)
      this.$toast.error('Error al cargar venta')
    })
    this.breakpoint()
  },
  computed: {

    selectedImage: function () {
      return this.imageSelected
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          this.properties.bottomBarHeight = '245px'
          this.properties.mainImageHeight = '275px'
          this.properties.iconSize = 17
          this.properties.fontSize = '13px'
          return true
        }
        case 'sm': {
          this.properties.bottomBarHeight = '315px'
          this.properties.iconSize = 17
          this.properties.fontSize = '13px'
          return (this.$vuetify.breakpoint.mobile || window.innerWidth < 600) || false
        }
        case 'md': {
          this.properties.bottomBarHeight = '382px'
          this.properties.iconSize = 21
          this.properties.fontSize = '16px'
          return false
        }
        case 'lg': {
          this.properties.bottomBarHeight = '382px'
          this.properties.iconSize = 21
          this.properties.fontSize = '16px'
          return false
        }
        case 'xl': {
          this.properties.bottomBarHeight = '382px'
          return false
        }
      }
    },
    isMobile () {
      if (window.innerWidth < 700 && this.$vuetify.breakpoint.mobile) {
        this.properties.mainImageHeight = '275px'
        return true
      }
      return false
    },

    innerWidth () {
      return window.innerWidth
    },

    random () {
      var x = parseInt(Math.random() * 10)
      return x > 5 ? x - 5 : x
    }
  },
  methods: {
    searchProducts () {
      alert('Searching...' + this.search)
    },
    getImgUrl (image) {
      // return process.env.VUE_APP_API_IMAGE_URL + image
      var images = require.context('../../assets/', false)
      return images('./' + image)
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 0;
}
.card-template{
  margin: 0 50px;
}
p {
  margin-bottom: 0;
}
.f-10 {
  font-size: 10px;
}
</style>
