<script>
import BackButton from '../../../components/BackButton.vue'
import Rating from '../../../components/Rating/Rating.vue'

export default {
  name: 'MyStore',
  components: {
    BackButton,
    Rating
  },
  data () {
    return {
      loading: false,
      address: '',
      createNewGarageSale: false,
      dialog: false,
      dialogDelete: false,
      editedIndex: -1,
      editedItem: {
        id: null,
        name: null,
        description: null,
        note: null,
        slug: null,
        phone: null,
        email: null,
        webSite: null,
        opening: null,
        closing: null,
        homeDelivery: false,
        deliveryMunicipalities: [],
        photo: 'no_image.jpeg',
        address: {
          mainStreet: null,
          province: null,
          municipality: null,
          coordenates: null
        },
        telegram: {
          username: null
        }
      },
      defaultItem: {
        id: null,
        name: null,
        description: null,
        slug: null,
        phone: null,
        email: null,
        webSite: null,
        homeDelivery: false,
        photo: 'no_image.jpeg',
        address: {
          mainStreet: null,
          province: null,
          municipality: null,
          coordenates: null
        },
        workDays: []
      },
      options: {
        duration: 300,
        offset: 150,
        easing: 'easeInOutCubic'
      },
      properties: {
        mainImageWidth: 450,
        mainImageHeight: 398,
        bottomBarHeight: '245px'
      },
      store: null
    }
  },
  methods: {
    createStore () {
      this.$router.push({ name: 'create-store', params: { store: this.defaultItem } })
    },

    showProductList () {
      this.$router.push({ name: 'store-products', params: { store: this.defaultItem.products } })
    },

    getImgUrl (image) {
      // return process.env.VUE_APP_API_IMAGE_URL + image
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    },

    editItem (item) {
      this.editedItem = Object.assign({}, item)
      this.createNewMenu = true
    },

    deleteItem (item) {
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    }
  }
}
</script>

