<template>
  <v-main>
    <v-container class='kaxsdc' data-event='load' style="margin-top: 0 !important;">
      <v-btn class="text-bold" text color="primary" @click="goBack" style="font-size: 12px; margin-bottom: 30px;">
        <v-icon class="mr-2">mdi-arrow-left</v-icon> Volver
      </v-btn>
      <v-row align="center" justify="space-between">
        <v-col cols="auto"><h1 class="text-bolder">Información de pago</h1></v-col>
        <v-col cols="auto">
          <v-row align="center" justify="space-between" justify-sm="space-between end">
            <v-col cols="auto">
              <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/Visa@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
            </v-col>
            <v-col cols="auto">
              <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/MasterCard@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
            </v-col>
            <v-col cols="auto">
              <v-img width="64" max-width="64" height="40" max-height="40" src="../../../assets/pay-order/American_Express@2x.png" lazy-src="../../../assets/pay-order/Visa@2x.png"></v-img>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-card class="rounded-xl pt-5">
        <ValidationObserver ref="observer">
          <form autocomplete="false" @submit.prevent="payOrder">
            <v-container class="mt-0">
                <v-row justify="space-between">
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-1 order-md-1 order-sm-1 order-xs-1">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="ccNum"
                      rules="required|min:16|max:16"
                    >
                      <label for="ccNum">Número de tarjeta</label>
                      <v-text-field
                        ref="ccNum"
                        id="ccNum"
                        v-model="payment.card.ccNum"
                        placeholder="Todos los dígitos de su tarjeta"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        maxlength="16"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-2 order-md-2 order-sm-2 order-xs-2">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="cvv"
                      rules="required|max:4"
                    >
                      <label for="cvv">Dígitos de seguridad</label>
                      <v-text-field
                        ref="cvv"
                        id="cvv"
                        v-model="payment.card.cvv"
                        placeholder="CVV2/CVC2"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-3 order-md-3 order-sm-9 order-xs-3">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="state"
                      rules="required"
                    >
                      <label for="state">Estado</label>
                      <v-select
                        ref="state"
                        id="state"
                        v-model="payment.card.state"
                        :items="states"
                        placeholder="Seleccionar estado"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="4" sm="6" xs="12" class="order-4 order-md-4 order-sm-3 order-xs-3">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_month"
                      rules="required"
                    >
                      <label for="expiration_month">Mes de vencimiento</label>
                      <v-select
                        ref="expiration_month"
                        id="expiration_month"
                        v-model="payment.card.ccExp[0]"
                        :items="months"
                        item-text="name"
                        item-value="value"
                        placeholder="Seleccionar mes"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-5 order-md-5 order-sm-4 order-xs-4">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="expiration_year"
                      rules="required"
                    >
                    <label for="expiration_year">Año de vencimiento</label>
                      <v-select
                        ref="expiration_year"
                        id="expiration_year"
                        v-model="payment.card.ccExp[1]"
                        :items="years"
                        item-text="name"
                        item-value="value"
                        placeholder="Año de vencimiento"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-6 order-md-6 order-sm-10 order-xs-11">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="city"
                      rules="required"
                    >
                      <label for="city">Ciudad</label>
                      <v-select
                        ref="city"
                        id="city"
                        v-model="payment.card.city"
                        :items="cities"
                        placeholder="Seleccione un estado"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        no-data-text="No hay datos"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-select>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="4" sm="6" xs="12" class="order-7 order-md-7 order-sm-5 order-xs-5">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="first_name"
                      rules="required"
                    >
                      <label for="first_name">Nombre</label>
                      <v-text-field
                        ref="first_name"
                        id="first_name"
                        v-model="payment.card.firstName"
                        placeholder="Escriba su nombre"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-8 order-md-8 order-sm-6 order-xs-6">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="last_name"
                      rules="required"
                    >
                      <label for="last_name">Apellidos</label>
                      <v-text-field
                        ref="last_name"
                        id="last_name"
                        v-model="payment.card.lastName"
                        placeholder="Escriba sus apellidos"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-9 order-md-9 order-sm-11 order-xs-11">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="zip"
                      rules="required"
                    >
                      <label for="last_name">Código postal</label>
                      <v-text-field
                        ref="zip"
                        id="zip"
                        v-model="payment.card.zip"
                        placeholder="Código Zip"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>

                  <v-col cols="12" md="4" sm="6" xs="12" class="order-10 order-md-10 order-sm-7 order-xs-7">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="address1"
                      rules="required"
                    >
                      <label for="address1">Dirección (Linea 1)</label>
                      <v-text-field
                        ref="address1"
                        id="address1"
                        v-model="payment.card.address1"
                        placeholder="Dirección de registro"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" class="order-11 order-md-11 order-sm-8 order-xs-8">
                    <ValidationProvider
                      v-slot="{ errors }"
                      name="address2"
                    >
                      <label for="address2">Dirección (Linea 2)</label>
                      <v-text-field
                        ref="address2"
                        id="address2"
                        v-model="payment.card.address2"
                        placeholder="Dirección de registro"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                  </v-col>
                  <v-col cols="12" md="4" sm="6" xs="12" align="center" align-self="center" class="pt-3 order-12 order-md-12 order-sm-12 order-xs-12">
                    <h1 class="text-bolder text-end d-flex flex-row justify-end"><p class="mr-5" style="color: black !important">Total:</p><p class="text-info text-bold">${{ payment.amount }}</p></h1>
                  </v-col>
                </v-row>
                <v-row align="center" justify="space-between" class="py-5 px-3">
                  <v-btn type="button" class="mx-2 rounded-lg text-bold border-hidden" outlined rounded elevation="3" color="secondary" @click="reset">Borrar todo</v-btn>
                  <p v-if="breakpoint" class="mb-0">Todos los capos son requeridos</p>
                  <v-btn type="submit" class="mx-2 rounded-lg text-bold" elevation="3" color="secondary">Pagar</v-btn>
                </v-row>
            </v-container>
          </form>
        </ValidationObserver>
      </v-card>
    </v-container>

    <v-dialog max-width="430" v-model="paymentCompleteDialog">
      <v-card>
        <v-row align="center">
          <v-col align="center" class="pt-4 pb-0">
            <v-card-text>
                  <v-icon size="60px" :color="dialogData.color">{{ dialogData.icon}}</v-icon>
              <h2 class="my-5" :style="'color: '+dialogData.color">{{ dialogData.title}}</h2>
              <h3 :style="'color: '+dialogData.color">{{ dialogData.text}}</h3>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn class="text-bold" text
                :color="dialogData.color"
                @click="exit">
              Cerrar
            </v-btn>
            </v-card-actions>
            </v-col>
          </v-row>
      </v-card>
    </v-dialog>
  </v-main>
</template>

<script type='text/javascript' :src="(process.env.NODE_ENV === 'production' ? process.env.VUE_APP_DC_URL : process.env.VUE_APP_DC_TEST_URL) + 'collect/sdk?m=' + process.env.VUE_APP_RIS_MERCHANTID + '&s=' + payment.kount.sess"></script>
<script type='text/javascript'>
  var client=new ka.ClientSDK()
  client.autoLoadEvents()
</script>
<script>
import states from '@/nomencladores/states.json'
import paymentServices from '@/axios/payment.services'
import contactsServives from '@/axios/contacts.services'
import router from '../../../router'
import paymentErrorHandler from '@/helpers/handle.errors'
import requestServices from '@/axios/request.services'

export default {
  name: 'PayOrder',
  data: function () {
    return {
      states: [],
      months: [],
      years: [],
      payment: this.$route.params.payment,
      request: this.$route.params.request,
      paymentCompleteDialog: false,
      dialogData: {
        color: null,
        icon: null,
        title: null,
        text: null
      }
    }
  },
  created () {
    if (!this.$route.params.request) {
      router.replace('/carito')
    }

    for (const key in states) {
      this.states.push(key)
    }

    this.months = [
      { name: '(01) Enero', value: '01' },
      { name: '(02) Febrero', value: '02' },
      { name: '(03) Marzo', value: '03' },
      { name: '(04) Abril', value: '04' },
      { name: '(05) Mayo', value: '05' },
      { name: '(06) Junio', value: '06' },
      { name: '(07) Julio', value: '07' },
      { name: '(08) Agosto', value: '08' },
      { name: '(09) Septiembre', value: '09' },
      { name: '(10) Octubre', value: '10' },
      { name: '(11) Noviembre', value: '11' },
      { name: '(12) Diciembre', value: '12' }
    ]

    const currentYear = new Date().getFullYear()
    for (let i = 9; i > 0; i--) {
      var y1 = parseInt(currentYear) + parseInt(i)
      this.years.push({
        name: y1,
        value: y1.toString().substring(2)
      })
    }
    this.years.push({
      name: currentYear,
      value: currentYear.toString().substring(2)
    })
    for (let i = 0; i < 9; i++) {
      var y2 = parseInt(currentYear) - parseInt(i)
      this.years.push({
        name: y2,
        value: y2.toString().substring(2)
      })
    }
  },
  computed: {
    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return false
        case 'sm': return false
        // case 'md': return false
        // case 'lg': return false
        // case 'xl': return false
      }
    },

    cities () {
      return states[this.payment.card.state]
    }
  },
  methods: {
    goBack () {
      return router.back()
    },

    payOrder () {
      this.$refs.observer.validate().then((result) => {
        if (result) {
          var exp = this.payment.card.ccExp[0]+this.payment.card.ccExp[1]
          this.payment.card.ccExp = exp
          this.cleanData()
          this.payment['generic'] = {
            object: this.request,
            type: 'request'
          }
          paymentServices.makePay(this.payment).then((result) => {
            if (result && result.data.ok) {

              requestServices.addRequest(this.request).then((result) => {
                if (result) {
                  this.dialogData = {
                    icon: 'mdi-check-circle-outline',
                    color: 'primary',
                    title: '¡Gracias por preferirnos!',
                    text: 'Pago realizado éxito'
                  }
                  this.paymentCompleteDialog = true
                  this.$store.dispatch('removeRestaurantProductsFromCart', this.request.store)
                }
              }).catch((err) => {
                this.dialogData = {
                  icon: 'mdi-close-circle-outline',
                  color: '#b71c1c',
                  title: '¡Error al realizar el pedido!',
                  text: ''
                }
                this.paymentCompleteDialog = true
              })

            } else {
              if (result.data.name === 'SuspiciousTransaction') {
                this.$store.dispatch('removeRestaurantProductsFromCart', this.request.store)
                this.dialogData = {
                  icon: 'mdi-alert-circle-outline',
                  color: '#FF5722',
                  title: '¡Gracias por preferirnos!',
                  text: 'Su transacción está siendo revisada'
                }
                this.paymentCompleteDialog = true
              } else {
                this.dialogData = {
                  icon: 'mdi-close-circle-outline',
                  color: '#b71c1c',
                  title: '¡Error al realizar el pago!',
                  text: ''
                }
                this.paymentCompleteDialog = true
              }
            }
          }).catch((err) => {
            this.dialogData = {
              icon: 'mdi-close-circle-outline',
              color: '#b71c1c',
              title: '¡Error al realizar el pago!',
              text: ''
            }
            this.paymentCompleteDialog = true
          })
        }
      })
    },

    expiredDate (month, year) {
      this.ccExp = month + year
    },

    reset () {
      for (const key in this.$refs) {
        if (key !== 'observer' && key !== 'open_date_menu' && key !== 'closed_date_menu') {
          this.$refs[key].reset()
        }
      }
    },

    cleanData () {
      for (const key in this.request) {
        if (this.request[key] === null) {
          delete this.request[key]
        }
      }
    },

    exit () {
      if (this.$store.getters.cart().length > 0) {
        router.push('/carrito')
      } else {
        router.push('/')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  margin: 3rem auto;
}
.row{
  margin: 0 !important;
}
.col{
  padding-top: 0px;
  // padding-bottom: 0px;
}
h1 p{
  margin-bottom: 0 !important;
}
.flex-alignment{
  flex-direction: row !important;
  display: flex !important;
}
</style>
