import axiosCustom from '../axios'

export default class RequestServices {
  static getAllRequests (data) {
    return axiosCustom.get(`/service/store/${data.storeId}/request`, { params: {} })
  }

  static getRequestById (data) {
    return axiosCustom.get(`/service/store/${data.storeId}/request/${data.requestId}`)
  }

  static addRequest (data) {
    return axiosCustom.post(`/service/store/${data.store}/request`, data)
  }

  static updateRequest (data) {
    return axiosCustom.patch(`/service/store/${data.storeId}/request/${data.requestId}`, data.object)
  }

  static deleteRequest (data) {
    return axiosCustom.delete(`/service/store/${data.storeId}/request/${data.requestId}`)
  }
}
