<template>
  <v-main>
    <v-container id="wish-list-container" class="py-14" style="min-height: 76vh">
      <h1 class="text-bolder">Lista de deseos</h1>

        <v-row class="my-7 justify-center" dense>
          <v-col cols="10">
            <v-row v-for="(product, i) in products" :key="i" align="center" justify="center">
              <v-col cols="12" md="1" sm="3" xs="12" align="center" v-if="innerWidth > '960'">
                <v-btn max-width="55" min-width="55" class="border-hidden rounded-lg" outlined elevation="3"
                       color="secondary"
                       style="background: white !important"
                       @click="removeFromFavoriteProducts(product)">
                  <v-icon>mdi-close</v-icon>
                </v-btn>
              </v-col>
              <v-col cols="12" md="11" sm="9" xs="12">
                <MenuProduct :product="product" :horizontal="true" share :remove="innerWidth < '960'" @removeFromFavorite="removeFromFavoriteProducts(product)" />
              </v-col>
            </v-row>
          </v-col>
        </v-row>
    </v-container>
  </v-main>
</template>

<script>
import userServices from '@/axios/user.services'
import MenuProduct from '@/components/MenuProduct'
import router from '../../../router'

export default {
  name: 'WishList',
  components: {
    MenuProduct
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.favoriteProducts(),
      pageProducts: [],
      stores: []
    }
  },

  created () {
    if (!this.$store.getters.userIsLogued()) {
      router.replace('/iniciar')
    } else {
      userServices.getWishList().then((result) => {
        if (result) {
          this.products = result.data.wishList.offers
        }
      }).catch(() => {
        this.$toast.error('No se pudo cargar el listado')
      })
    }
  },
  computed: {
    innerWidth () {
      return window.innerWidth
    }
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
      this.removeFromFavoriteProducts(product)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
    },
    removeFromFavoriteProducts (product) {
      userServices.removeToWishList(product._id).then((result) => {
        if (result) {
          this.$store.dispatch('removeFromFavoriteProducts', product)
          userServices.getWishList().then((result) => {
            if (result) {
              this.products = result.data.wishList.offers
            }
          }).catch(() => {
            this.$toast.error('No se pudo cargar el listado')
          })
        }
      }).catch(() => {
        this.$toast.error('No se pudo eliminar de favoritos')
      })
    },

    checkOnFavorites (product) {
      if (this.$store.getters.checkProductOnFavorites(this.product._id) === 0) {
        this.$store.dispatch('addToFavoriteProducts', product)
        product.isFavorite = true
      } else {
        this.$store.dispatch('removeFromFavoriteProducts', product)
        product.isFavorite = false
      }
    },
    getImgUrl (image) {
      var images = require.context('../../../assets/', false)
      return images('./' + image)
    },
    goToDetails (product) {
      router.push({ path: '/stores/' + this.product.section.toLowerCase() + '/' + this.product._id + '/details' })
    }
  }
}
</script>

<style lang="scss" scoped>
p{
  margin-bottom: 0;
}
.row{
  margin: 0;
}
.v-btn.success{
  background-color: #309999 !important;
  color: white !important;
  border-radius: 9px;
}
</style>
