<template>
  <v-main>
    <v-container id="order-confirm-container" :style="`padding: 12px ${containerWidth};`" v-if="!loading">
       <v-banner
        v-model="showBanner"
        elevation="10"
        transition="slide-y-transition"
        color="secondary"
        sticky
        style="color: white"
      >
        El destino supera la distancia máxima de entrega
      </v-banner>

      <h1 class="my-14">COLOCAR LA ORDEN</h1>
      <v-data-table
        :headers="headers"
        :items="products"
        hide-default-footer
        class="elevation-0"
        dense
      >

        <template v-slot:item.title="{ item }">
          <v-col align="center" class="px-0">
            <p id="title" @click="goToDetails(item)">{{ item.name }}</p>
          </v-col>
        </template>

        <template v-slot:item.amount="{ item }">
          <v-col class="px-0">
            <p class="text-center text-info text-bold">{{ item.amount }}</p>
          </v-col>
        </template>

        <template v-slot:item.price="{ item }">
          <v-col class="px-0">
            <p class="text-center text-info text-bold">${{ Number.parseFloat(item.price).toFixed(2) }}</p>
          </v-col>
        </template>

        <template v-slot:item.subtotal="{ item }">
          <v-col class="pl-0">
            <p class="text-center text-info text-bold">${{ Number.parseFloat(item.price * item.amount).toFixed(2) }}</p>
          </v-col>
        </template>

        <template v-slot:item.divider="{ item }">
          <v-divider vertical inset class="row-divider"
            :style="breakpoint ? 'display: none;' : ''">
          </v-divider>
        </template>

        <template v-slot:no-data>
          <span>No hay nada</span>
        </template>

      </v-data-table>

      <div v-if="products.length > 0" id="to-pay-info" class="mt-5">
        <v-row align="center" justify="end">
          <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
            <span>Subtotal:</span>
          </v-col>
          <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
            <span class="mx-3">${{ calculateSubtotal }}</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
            <span>Precio de Envío:</span>
          </v-col>
          <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
            <span class="mx-3">${{ costShipping }}</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
            <span>Impuesto por la compra:</span>
          </v-col>
          <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
            <span class="mx-3">${{ calculatetax }}</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="6">
            <v-divider class="mt-3" inset></v-divider>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col cols="8" md="5" sm="5" xs="8" class="text-end text-bold" style="font-family: 'Raleway-bold">
            <span>Total</span>
          </v-col>
          <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
            <span class="mx-3">${{ calculatTotal }}</span>
          </v-col>
        </v-row>
      </div>

      <v-row class="mb-16" justify="space-between" align="center">
        <v-col cols="12" md="6" sm="12" xs="12">
          <span id="comment">*Inserte la dirección para conocer el costo del envío</span>
        </v-col>
      </v-row>
      <div v-if="products.length > 0">
        <v-row align="center">
          <v-col cols="12">
            <label for="field1">Seleccionar destinatario</label>
            <v-autocomplete
              id="field1"
              v-model="client"
              :items="contacts"
              item-text="info"
              item-value="_id"
              color="primary"
              dense
              clearable
              outlined
              mandatory
              no-data-text="No hay contactos"
              @change="loadContactInformation($event)"
            >
            </v-autocomplete>
          </v-col>
          <!-- <v-col cols="auto" align="center">
            <v-btn rounded color="primary" @click="show = !show">
              Agregar
            </v-btn>
          </v-col> -->
        </v-row>
      </div>

      <!-- <NewClient v-if="show" @newDestinatarySaved="addNewDestinatary($event)" @cancel="show = false" /> -->

      <!-- <v-row justify="space-between" class="px-3" v-if="products.length > 0 && !show">
          <v-col md="6" sm="12" xs="12">
            <ValidationProvider
            v-slot="{ errors }"
            name="alternativeDestinatary"
            >
              <v-text-field
                id="alternativeDestinatary"
                v-model="request.client.alternativeDestinatary"
                label="Nombre del destinatario alternativo"
                outlined
                dense
                class="mx-auto rounded-lg"
                style="width: 430px !important"
                :error-messages="errors"
                :hide-details="errors.length > 0 ? false : true"
                @input="client.deny_alternative_destinatary = false"
              ></v-text-field>
            </ValidationProvider>
          </v-col>
          <v-col md="6" sm="12" xs="12">
            <v-menu
            ref="date_menu"
            v-model="date_menu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <ValidationProvider
                v-slot="{ errors }"
                name="deliveryDate"
                rules="required"
              >
                <v-text-field
                  id="deliveryDate"
                  v-model="request.client.deliveryDate"
                  label="Fecha y hora del envío"
                  color="primary"
                  persistent-hint
                  prepend-inner-icon="mdi-calendar"
                  v-bind="attrs"
                  @blur="date = parseDate(client.deliveryDate)"
                  v-on="on"
                  class="mx-auto rounded-lg"
                  style="width: 430px !important"
                  :error-messages="errors"
                  :hide-details="errors.length > 0 ? false : true"
                  clearable
                  dense
                  outlined
                ></v-text-field>
              </ValidationProvider>
            </template>
            <v-date-picker
              v-model="date"
              no-title
              color="primary"
              @input="date_menu = false"
            ></v-date-picker>
          </v-menu>
          </v-col>
      </v-row>

      <v-row justify="space-between" class="px-3" v-if="products.length > 0 && !show">
        <v-checkbox
          v-model="request.client.deny_alternative_destinatary"
          label="Yo confirmo no dar destinatario alternativo"
          :disabled="client.alternativeDestinatary != null && client.alternativeDestinatary != ''"
        ></v-checkbox>
      </v-row> -->

      <v-card elevation="3" class="px-2 px-md-9 px-sm-4 px-xs-2 py-4 rounded-xl">
        <ValidationObserver ref="observer">
          <!-- <form autocomplete="false" @submit.prevent="saveNewDestinatary"> -->
            <h2 style="margin: 25px 1rem; font-family: 'Raleway-bold">Destinatario</h2>
            <v-container style="padding: 12px 0;">

              <!-- Envio -->
              <div class="mt-4 pa-3">
                <v-radio-group v-model="homeDelivery" row @change="getDistancePrice">
                  <v-radio class="mx-2 mt-0" label="Enviar a domicilio" :value="true" :disabled="storeHomeDelivery"></v-radio>
                  <v-radio class="mx-2 mt-0" label="Recogida en el local" :value="false"></v-radio>
                </v-radio-group>
              </div>

              <!-- Nombre y apellidos -->
              <v-row justify="space-between">
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="name"
                    rules="required|alpha_spaces"
                  >
                    <label for="name">Nombre*</label>
                    <v-text-field
                      id="name"
                      v-model="request.client.name"
                      placeholder="Nombre completo"
                      outlined
                      dense
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                      v-slot="{ errors }"
                      name="lastname"
                      rules="required|alpha_spaces"
                    >
                    <label for="lastname">Apellidos*</label>
                  <v-text-field
                    id="lastname"
                    v-model="request.client.lastName"
                    placeholder="Apellidos completos"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                  ></v-text-field>
                </ValidationProvider>
                </v-col>
              </v-row>

              <!-- Telefono y carnet -->
              <v-row justify="space-between">
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="phone"
                    rules="required|min:7|numeric"
                  >
                    <label for="phone">Teléfono*</label>
                    <v-text-field
                      id="phone"
                      v-model="request.client.phone"
                      placeholder="Línea cubana"
                      outlined
                      dense
                      prefix="+53"
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="ci"
                    rules="required|min:11|max:11"
                  >
                  <label for="ci">Carnet de indentidad*</label>
                    <v-text-field
                      id="ci"
                      v-model="request.client.ci"
                      placeholder="Número completo"
                      outlined
                      dense
                      class="mx-auto rounded-lg"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      maxlength="11"
                    ></v-text-field>
                  </ValidationProvider>
                </v-col>
              </v-row>

              <!-- Direccion -->
              <v-row v-if="homeDelivery">
                <v-col>
                  <label for="">Dirección*</label>
                  <v-row class="ma-0 py-3">
                    <v-btn height="26" :color="invalidCoordenates && !this.request.client.address.coordenates ? 'error' : 'primary'" outlined rounded style="text-transform: none; font-size: smaller; letter-spacing: initial;" @click="markPosition = true">
                      <v-icon small>mdi-map-marker</v-icon>
                      Ubicar en el mapa
                    </v-btn>
                  </v-row>
                  <v-row align="center" class="px-0">
                    <v-col cols="12" md="4" sm="12" xs="12" class="pl-0">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="mainStreet"
                          :rules="homeDelivery ? 'required' : ''"
                        >
                      <v-text-field
                        id="mainStreet"
                        v-model="request.client.address.mainStreet"
                        placeholder="Calle principal"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                      </ValidationProvider>
                    </v-col>
                    entre
                    <v-col cols="12" md="4" sm="12" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="beetwenX"
                          :rules="homeDelivery ? 'required' : ''"
                        >
                      <v-text-field
                        id="beetwenX"
                        v-model="request.client.address.beetwenX"
                        placeholder="Calle"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    y
                    <v-col cols="12" md="3" sm="12" xs="12" class="pr-0">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="beetwenY"
                          :rules="homeDelivery ? 'required' : ''"
                        >
                      <v-text-field
                        id="beetwenY"
                        v-model="request.client.address.beetwenY"
                        placeholder="Calle"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>

                    <v-col cols="12" md="2" sm="4" xs="12" class="pl-0">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="houseNumber"
                          :rules="homeDelivery ? 'required' : ''"
                        >
                      <v-text-field
                        id="houseNumber"
                        v-model="request.client.address.houseNumber"
                        placeholder="#"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <v-col cols="12" md="2" sm="4" xs="12">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="apartment"
                        >
                      <v-text-field
                        id="apartment"
                        v-model="request.client.address.apartment"
                        placeholder=".Apt"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col>
                    <!-- <v-col cols="12" md="3" sm="4" xs="12" class="pr-0">
                      <ValidationProvider
                          v-slot="{ errors }"
                          name="zip"
                        >
                      <v-text-field
                        id="zip"
                        v-model="request.client.address.zip"
                        placeholder="Codigo postal"
                        outlined
                        dense
                        class="mx-auto rounded-lg"
                        :error-messages="errors"
                        :hide-details="errors.length > 0 ? false : true"
                        @input="getDistancePrice"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-col> -->
                  </v-row>
                </v-col>
              </v-row>

              <!-- Provincia y municipio -->
              <v-row justify="space-between" v-if="homeDelivery">
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="province"
                    :rules="homeDelivery ? 'required' : ''"
                  >
                    <label for="province">Provincia*</label>
                    <v-select
                      ref="province"
                      id="province"
                      v-model="request.client.address.province"
                      :items="provinces"
                      item-text="name"
                      item-value="name"
                      placeholder="Selecionar provincia"
                      outlined
                      dense
                      class="mx-auto rounded-lg"
                      no-data-text="No hay datos"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      @change="getDistancePrice()"
                    ></v-select>
                  </ValidationProvider>
                </v-col>
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="municipality"
                    :rules="homeDelivery ? 'required' : ''"
                  >
                    <label for="municipality">Municipio*</label>
                    <v-select
                      ref="municipality"
                      id="municipality"
                      v-model="request.client.address.municipality"
                      :items="municipalities"
                      placeholder="Seleccionar municipio"
                      outlined
                      dense
                      class="mx-auto rounded-lg"
                      no-data-text="No hay datos"
                      :error-messages="errors"
                      :hide-details="errors.length > 0 ? false : true"
                      @change="getDistancePrice()"
                    ></v-select>
                  </ValidationProvider>
                </v-col>
              </v-row>

              <!-- destinatario alternativo y fecha de envio -->
              <v-row justify="space-between">
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                      v-slot="{ errors }"
                      name="alternativeDestinatary"
                    >
                    <label for="alternativeDestinatary">Destinatario alternativo</label>
                  <v-text-field
                    id="alternativeDestinatary"
                    v-model="request.client.alternativeDestinatary.name"
                    placeholder="Nombre completo"
                    outlined
                    dense
                    class="mx-auto rounded-lg"
                    :error-messages="errors"
                    :hide-details="errors.length > 0 ? false : true"
                    @input="request.client.alternativeDestinatary.deny = false"
                  ></v-text-field>
                </ValidationProvider>
                </v-col>
                <v-col cols="12" md="6" sm="12" xs="12">
                  <label for="alternativeDestinatary">Fecha y hora del envío*</label>
                  <Datetime
                    v-model="request.deliveryDate"
                    format="DD/MM/YYYY h:i"
                    :readonly="true"
                    :required="true"
                    placeholder="Seleccionar fecha y hora"
                  >
                  </Datetime>
                </v-col>
              </v-row>

              <!-- confirmar no destinatario alternativo -->
              <v-row justify="space-between">
                <v-col cols="12" md="6" sm="12" xs="12">
                  <ValidationProvider
                      v-slot="{ errors }"
                      name="no_alternative"
                      ref="no_alternative"
                      rules="required">
                    <v-checkbox
                      id="no_alternative"
                      v-model="request.client.alternativeDestinatary.deny"
                      class="text-info"
                      label="Yo confirmo no dar destinatario alternativo"
                      :error-messages="errors"
                      :disabled="request.client.alternativeDestinatary.name != null && request.client.alternativeDestinatary.name != ''"
                    ></v-checkbox>
                  </ValidationProvider>
                </v-col>
              </v-row>

              <!-- note -->
              <v-row>
                <v-col cols="12">
                  <label for="note">Nota al storee</label>
                  <v-textarea
                    ref="note"
                    id="note"
                    name="note"
                    v-model="request.note"
                    placeholder="Breve nota del storee"
                    outlined
                    hide-details
                    rows="3"
                    class="rounded-lg"
                  ></v-textarea>
                </v-col>
              </v-row>

          </v-container>
        <!-- </form> -->
        </ValidationObserver>
      </v-card>

      <v-row class="my-16 px-4" justify="space-between" align="center">
        <v-btn class="text-bold close-btn mx-2" outlined color="secondary" to="/carrito">
          <v-icon class="mr-2">mdi-arrow-left</v-icon> Cambiar la orden
        </v-btn>
        <v-btn :class="products.length > 0 ? 'text-bold mx-2 pr-3 close-btn' : 'text-bold mx-2 pr-3'"
                color="primary"
                :disabled="products.length === 0 || !formIsValid || showBanner"
                @click="showOrderResume">
          Continuar <v-icon class="ml-2">mdi-arrow-right</v-icon>
        </v-btn>
      </v-row>

    </v-container>
    <v-container id="order-confirm-container" :style="`padding: 12px ${containerWidth}; height: 76vh;`" v-if="loading">
      <v-row>
        <v-col align="center">
          <v-progress-circular
          :size="110"
          :width="10"
          color="primary"
          indeterminate
          style="margin-top: 7rem"
        ></v-progress-circular>
        </v-col>
      </v-row>
    </v-container>

    <GoogleMapsMarker
      :showMap="markPosition"
      :currentPosition="request.client.address.coordenates"
      @closeMap="markPosition = false"
      @savePosition="updateCoordenates($event)" />

    <v-dialog id="shopping-dialog-detail" scrollable v-model="dialog" max-width="700">
      <v-card>
        <v-card-title><h3>Resumen de compra</h3></v-card-title>
        <v-card-text style="height: 495px">
          <v-data-table
            :headers="headers"
            :items="products"
            hide-default-footer
            class="elevation-0"
            dense
          >

            <template v-slot:item.title="{ item }">
              <v-col align="center" class="px-0">
                <p id="title" @click="goToDetails(item)">{{ item.name }}</p>
              </v-col>
            </template>

            <template v-slot:item.amount="{ item }">
              <v-col class="px-0">
                <p class="text-center text-info text-bold">{{ item.amount }}</p>
              </v-col>
            </template>

            <template v-slot:item.price="{ item }">
              <v-col class="px-0">
                <p class="text-center text-info text-bold">${{ Number.parseFloat(item.price).toFixed(2) }}</p>
              </v-col>
            </template>

            <template v-slot:item.subtotal="{ item }">
              <v-col class="pl-0">
                <p class="text-center text-info text-bold">${{ Number.parseFloat(item.price * item.amount).toFixed(2) }}</p>
              </v-col>
            </template>

            <template v-slot:item.divider="{ item }">
              <v-divider vertical inset class="row-divider"
                :style="breakpoint ? 'display: none;' : ''">
              </v-divider>
            </template>

            <template v-slot:no-data>
              <span>No hay nada</span>
            </template>

          </v-data-table>

          <div v-if="products.length > 0" id="to-pay-info" class="mt-5">
            <v-row align="center" justify="end">
              <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                <span>Subtotal:</span>
              </v-col>
              <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                <span class="mx-3">${{ calculateSubtotal }}</span>
              </v-col>
            </v-row>
            <v-row align="center" justify="end">
              <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                <span>Precio de Envío:</span>
              </v-col>
              <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                <span class="mx-3">${{ costShipping }}</span>
              </v-col>
            </v-row>
            <v-row align="center" justify="end">
              <v-col cols="8" md="5" sm="5" xs="8" class="text-end">
                <span>Impuesto por la compra:</span>
              </v-col>
              <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                <span class="mx-3">${{ calculatetax }}</span>
              </v-col>
            </v-row>
            <v-row align="center" justify="end">
              <v-col cols="6">
                <v-divider class="mt-3" inset></v-divider>
              </v-col>
            </v-row>
            <v-row align="center" justify="end">
              <v-col cols="8" md="5" sm="5" xs="8" class="text-end text-bold" style="font-family: 'Raleway-bold">
                <span>Total</span>
              </v-col>
              <v-col cols="4" md="2" sm="2" xs="4" class="text-end text-info text-bold">
                <span class="mx-3">${{ calculatTotal }}</span>
              </v-col>
            </v-row>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-row class="pb-4 px-4" justify="space-between" align="center">
            <v-btn class="text-bold close-btn mx-2" outlined color="secondary" @click="dialog = false">
              Cerrar
            </v-btn>
            <v-btn :class="products.length > 0 ? 'text-bold mx-2 pr-3 close-btn' : 'text-bold mx-2 pr-3'"
                    color="primary"
                    :disabled="products.length === 0 || !formIsValid || showBanner"
                    @click="makeRequest">
              Pagar <v-icon class="ml-2">mdi-arrow-right</v-icon>
            </v-btn>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-main>
</template>

<script>
// import NewClient from './new-client'
import router from '../../../router'
import provinces from '@/nomencladores/provinces.json'
import storeServices from '@/axios/store.services'
import moment from 'moment'
import Datetime from '@/components/Datetime.vue'
import contactsServices from '@/axios/contacts.services'
import GoogleMapsMarker from '@/components/GoogleMapsMarker/GoogleMapsMarker.vue'

export default {
  name: 'AcceptOrder',
  components: {
    // NewClient
    Datetime,
    GoogleMapsMarker
  },
  data () {
    return {
      showBanner: false,
      client: null,
      dialog: false,
      loading: false,
      show: false,
      markPosition: false,
      invalidCoordenates: false,
      date: null,
      date_menu: null,
      provinces: provinces,
      store: null,
      disabledHours: [],
      minDate: null,
      homeDelivery: true,
      request: {
        number: null,
        state: null,
        deliveryDate: null,
        delivered: null,
        costShipping: Number.parseFloat(0.00).toFixed(2),
        subtotal: null,
        total: null,
        distance: null,
        orders: null,
        note: null,
        client: {
          name: null,
          lastName: null,
          ci: null,
          phone: null,
          address: {
            houseNumber: null,
            apartment: null,
            mainStreet: null,
            beetwenX: null,
            beetwenY: null,
            municipality: null,
            province: null,
            coordenates: null
          },
          alternativeDestinatary: {
            name: null,
            deny: null
          }
        },
        store: null,
        user: JSON.parse(localStorage.getItem('store_user')).user._id
      },
      headers: [
        { text: 'Producto', align: 'center', value: 'title', width: '70%' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Cantidad', align: 'center', value: 'amount' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Precio x producto', align: 'center', value: 'price' },
        { text: '', align: 'center', value: 'divider' },
        { text: 'Subtotal', align: 'center', value: 'subtotal' }
      ],
      products: [],
      contacts: [],
      ricentUsedContact: [],
      containerWidth: '130px'
    }
  },
  mounted () {
    if (this.$route.params.toBuy) {
      this.loading = true
      // getting product to buy
      var cart = this.$route.params.toBuy
      cart.map((p) => {
        p.count = this.$store.getters.checkProductOnCart(p._id) || 0
        return p
      })
      cart = [...new Set(cart)]
      this.products = cart
      this.request.orders = cart

      // calculating the min date to request a delivery
      var today = new Date()
      this.minDate = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate()

      // getting the store information
      this.getStore()

      contactsServices.getUserContacts().then((result) => {
        if (result) {
          this.contacts = result.data.contacts.map(x => {
            x.info = x.name + ' ' + x.lastName
            x.info += x.alternativeDestinatary.name ? '(Dest. Alternativo: ' + x.alternativeDestinatary.name + ')' : ''
            return x
          })
        }
      }).catch(() => {

      })
      contactsServices.getUserMostFrequentContact().then((result) => {
        if (result) {
          this.ricentUsedContact = result.data
        }
      }).catch(() => {

      })

      this.loading = false
    } else {
      router.replace('/carrito')
    }
  },
  watch: {
    date (val) {
      this.request.deliveryDate = this.formatDate(this.date)
    }
  },
  computed: {
    municipalities: function () {
      if (this.request.client.address.province) {
        return this.provinces.find(p => p.name === this.request.client.address.province).municipalities
      }
      return []
    },

    storeHomeDelivery () {
      return this.store ? !this.store.homeDelivery : true
    },

    calculateSubtotal () {
      var subtotal = 0
      if (this.products.length > 0) {
        this.products.forEach(p => {
          subtotal += p.price * p.amount
        })
      }
      this.request.subtotal = Number.parseFloat(subtotal).toFixed(2)
      return Number.parseFloat(subtotal).toFixed(2)
    },

    costShipping () {
      return this.request.costShipping
    },

    calculatetax () {
      var tax = 0
      tax = (parseFloat(this.request.subtotal) + parseFloat(this.request.costShipping)) * parseFloat(0.05)
      return Number.parseFloat(tax).toFixed(2)
    },

    calculatTotal () {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.request.total = parseFloat(this.request.subtotal) + parseFloat(this.request.costShipping) + parseFloat(this.calculatetax)
      this.request.total = Number.parseFloat(this.request.total).toFixed(2)
      return this.request.total
    },

    breakpoint () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': {
          document.querySelectorAll('.row-divider').forEach(x => {
            x.parentElement.setAttribute('style', 'display: none')
          })
          this.containerWidth = this.$vuetify.breakpoint.mobile ? '12px' : '50px'
          return true
        }
        case 'sm': {
          document.querySelectorAll('.row-divider').forEach(x => {
            x.parentElement.removeAttribute('style')
          })
          this.containerWidth = this.$vuetify.breakpoint.mobile ? '12px' : '80px'
          return false
        }
        case 'md': {
          document.querySelectorAll('.row-divider').forEach(x => {
            // x.parentElement.removeAttribute('style')
          })
          this.containerWidth = '130px'
          if (window.innerWidth <= 1200 && window.innerWidth >= 960) {
            this.containerWidth = '30px'
          }
          return false
        }
        // case 'lg': return false
        // case 'xl': return false
      }

      return false
    }

  },
  methods: {
    makeRequest () {
      // const valid = true
      this.invalidCoordenates = false
      this.request.orders = this.products.map(p => {
        return {
          name: p.name,
          price: p.price,
          count: p.amount
        }
      })
      var payment = {
        card: {
          ccNum: null,
          cvv: null,
          ccExp: [null, null],
          firsName: null,
          lastName: null,
          address1: null,
          address2: null,
          city: null,
          state: null,
          zip: null,
          email: JSON.parse(localStorage.getItem('store_user')).user.email
        },
        kount: {
          sess: this.generateSessionId(),
          site: 'ALACASA'
        },
        amount: this.request.total,
        mode: 'Q',
        products: this.products.map(p => {
          return {
            name: p.name,
            description: p.description,
            type: p.category.name,
            add: p.add,
            count: p.amount,
            price: p.price
          }
        })
      }
      this.request.deliveryDate = moment(this.request.deliveryDate, 'DD/MM/YYYY HH:mm a').toISOString()
      if (!this.homeDelivery) {
        this.request.costShipping = 0
        delete this.request.client.address
      }
      router.push({ name: 'order-pay', params: { request: this.request, payment: payment } })
    },

    formIsValid () {
      this.$refs.observer.validate().then((result) => {
        return result
      })
    },
    addNewDestinatary (event) {
      this.show = false
      this.clients.push(event)
      this.client.push(event)
    },
    removeFromCart (product) {
      this.$store.dispatch('removeFromCart', product)
      this.products = this.$store.getters.cart()
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },

    getStore () {
      storeServices.getStoreById({ _id: this.products[0].store._id }).then((result) => {
        if (result) {
          this.store = result.data
          this.request.store = this.store._id
          if (this.store) {
            this.request.client.address.province = this.store.address.province
          }
          this.homeDelivery = this.store.homeDelivery
          this.getDistancePrice()
          const opening = this.store.opening.split(':')[0]
          const closing = this.store.opening.split(':')[0]
          const now = new Date().getHours().toString()
          this.disabledHours = [opening, closing, now]
          this.disabledHours = [...new Set(this.disabledHours)]
        }
      }).catch(() => {
        this.$toast.error('Error al cargar storee')
      })
    },

    getDistancePrice () {
      if (this.homeDelivery) {
        if (this.request.client.address.coordenates) {
          storeServices.calculateDeliveryDistace({ storeId: this.store._id, destinations: this.request.client.address.coordenates }).then((result) => {
            if (result) {
              this.request.distance = result.data.distance.value
              // this.request.costShipping = parseFloat(result.data.distance) * parseFloat(0.7)
              this.request.costShipping = Number.parseFloat(result.data.distanceCost).toFixed(2)

            // this.showBanner = !result.data.value
            }
          }).catch(() => {
          // this.showBanner = true
            this.$toast.error('Error al calcular precio de envío')
          })
        } else {
          this.showBanner = false
        }

        if (this.request.client.address.municipality) {
          this.showBanner = !this.store.deliveryMunicipalities.includes(this.request.client.address.municipality)
        }
      } else {
        this.request.costShipping = Number.parseFloat(0).toFixed(2)
      }
    },

    loadContactInformation (contactId) {
      if (contactId) {
        this.request.client = this.contacts.find(x => x._id === contactId)
        this.getDistancePrice()
      } else {
        this.request.client = {
          name: null,
          lastName: null,
          ci: null,
          phone: null,
          address: {
            houseNumber: null,
            apartment: null,
            mainStreet: null,
            beetwenX: null,
            beetwenY: null,
            municipality: null,
            province: null,
            coordenates: null
          },
          alternativeDestinatary: {
            name: null,
            deny: null
          }
        }
      }
    },

    generateSessionId () {
      const result = []
      const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789_-'
      const charactersLength = characters.length
      for (let i = 0; i < 32; i += 1) {
        result.push(characters.charAt(Math.floor(Math.random() * charactersLength)))
      }
      return result.join('')
    },

    updateCoordenates (event) {
      this.request.client.address.coordenates = event
      this.invalidCoordenates = this.request.client.address.coordenates === null
      if (!this.invalidCoordenates) {
        this.getDistancePrice()
      }
    },

    showOrderResume () {
      if (this.homeDelivery) {
        this.invalidCoordenates = this.request.client.address.coordenates === null
      } else {
        this.invalidCoordenates = false
      }
      this.$refs.observer.validate().then((result) => {
        if (result && !this.invalidCoordenates) {
          if (!this.request.client.alternativeDestinatary.name && !this.request.client.alternativeDestinatary.deny) {
            this.$refs.no_alternative.applyResult({
              errors: ['Si no tiene destinatario alternativo es requerido este campo'],
              valid: false, // boolean state
              failedRules: {}
            })
          } else {
            this.dialog = true
          }
        } else {
          this.$toast.warning('Rellene correctamente los campos')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.row{
  margin: 0;
}
p{
  margin-bottom: 0;
}
#to-pay-info{
  .col{
    padding: 12px 0 0 0 !important;
  }
}
.close-btn{
  box-shadow: 3px 1px 16px rgb(48 153 153 / 32%) !important;
  border: none;
}
.text-info, .text-end{
  font-size: 18px;
}
.color-grey {
  color: lightgrey
}
#comment {
  // white-space: nowrap;
  text-align: left;
  font-family: Raleway;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #ff6600;
}

</style>
