<template>
  <div :id="product.slug">
    <div id="vertical-card" v-if="!horizontal || verticalPosition">
      <v-hover>
        <v-scale-transition></v-scale-transition>
        <template v-slot:default="{ hover }">
          <v-card
            class="ma-2 mb-4 rounded-lg"
            :min-width="verticalPosition ? '' : '293'"
            width="385"
            :max-width="verticalPosition ? '' : '293'"
            height="430"
            :elevation="hover ? 6 : 2"

          >
            <!-- <v-card-title class="d-flex flex-row-reverse" v-if="!verticalPosition">
              <v-btn rounded small text>Combos mixtos</v-btn>
            </v-card-title> -->
            <v-img
              :src="getImgUrl(product.image)"
              :lazy-src="getImgUrl(product.image)"
              :min-width="verticalPosition ? '' : '293'"
              :max-width="verticalPosition ? '' : '293'"
              class="white--text align-end"
              style="border-top-left-radius: 8px !important; border-top-right-radius: 8px !important"
              height="235px"
              @click="goToDetails()"
            >
            </v-img>

            <v-card-text class="text--primary pb-0" style="height: 122px">
              <div @click="goToDetails()">{{ product.name }}</div>
              <div>{{ product.description }}</div>

              <!-- <div>Precio: {{ item.price }}</div> -->
            </v-card-text>

            <v-divider style="margin: 11px 15px 0 15px !important;"></v-divider>

            <v-card-actions>
              <v-col>
                <div id="price">${{ Number.parseFloat(product.price).toFixed(2) }}</div>
              </v-col>
              <v-col class="d-flex flex-row-reverse">
                <v-btn v-if="remove" icon @click="removeFromFavorite" class="mx-1">
                  <v-icon>mdi-trash-can-outline</v-icon>
                </v-btn>
                <v-btn icon @click="addToCart(product)" class="mx-1">
                  <v-badge
                    :content="amoutn"
                    :value="amoutn"
                    color="yellow darken-4"
                    overlap
                  >
                    <v-icon :color="amoutn > 0? 'rgba(255, 102, 0, 1)' : 'grey darken-1'">mdi-cart-variant</v-icon>
                  </v-badge>
                </v-btn>
                <!-- <v-btn v-if="share" icon @click="copyURL" class="mx-3">
                  <v-icon>mdi-share-variant</v-icon>
                </v-btn> -->
                <v-btn v-if="!share" icon @click.prevent="checkOnFavorites(product)" class="mx-1">
                  <v-icon :color="favorite ? 'red' : 'grey darken-1'">{{ favorite ? 'mdi-heart' : 'mdi-heart-outline' }}</v-icon>
                </v-btn>
              </v-col>
            </v-card-actions>
          </v-card>
        </template>
      </v-hover>
    </div>

    <div id="horizontal-card" v-if="horizontal && !verticalPosition">
        <v-card
            class="ma-7 rounded-xl"
            elevation="11"
            min-height="280"
            max-height="280"
        >
        <v-row class="ma-0" style="height: 260px !important; min-height: 280px !important; max-height: 280px !important;">
          <v-col cols="5" class="pa-0">
            <v-img class="rounded-xl" height="280" max-height="280" :src="getImgUrl(product.image)" :lazy-src="getImgUrl(product.image)" @click="goToDetails()"></v-img>
          </v-col>
          <v-col class="d-flex flex-column justify-space-between">
            <v-card-text class="text--primary" style="height: 190px">
              <p id="title" @click="goToDetails()">{{ product.name }}</p>
              <div id="product-info">{{ product.description }}</div>

              <!-- <div>Precio: {{ item.price }}</div> -->
            </v-card-text>

            <v-divider style="margin: 11px 15px 5px 15px !important;"></v-divider>

            <v-card-actions>
              <v-col>
                <div id="price">${{ Number.parseFloat(product.price).toFixed(2) }}</div>
              </v-col>
              <v-col class="d-flex flex-row-reverse">
                <v-btn icon @click="addToCart()" class="mx-3">
                  <v-badge
                    :content="amoutn"
                    :value="amoutn"
                    color="yellow darken-4"
                    overlap
                  >
                    <v-icon :color="amoutn > 0? 'rgba(255, 102, 0, 1)' : 'grey darken-1'">mdi-cart-variant</v-icon>
                  </v-badge>
                </v-btn>
                <!-- <v-btn v-if="share" icon @click="copyURL" class="mx-3">
                  <v-icon>mdi-share-variant</v-icon>
                </v-btn> -->
                <v-btn v-if="!share" icon @click.prevent="checkOnFavorites(product)" class="mx-3">
                  <v-icon :color="favorite ? 'red' : 'grey darken-1'">{{ favorite ? 'mdi-heart' : 'mdi-heart-outline' }}</v-icon>
                </v-btn>
              </v-col>
            </v-card-actions>
          </v-col>
        </v-row>
      </v-card>
    </div>

    <v-dialog v-model="shareProduct" max-width="500px">
      <v-card>
        <v-card-title>
          <v-text-field id="product_url" readonly outlined ref="product_url" :value="url" hide-details></v-text-field>
        </v-card-title>
        <v-card-actions class="d-flex flex-row justify-end">
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="copyURL">Copiar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-snackbar
      v-model="snackbar.show"
      :timeout="snackbar.timeout"
    >
      {{ snackbar.text }}

    </v-snackbar>
  </div>
</template>

<script>
import router from '../router'
import store from '../store'
import userServices from '@/axios/user.services'

export default {
  name: 'MenuProduct',
  props: {
    product: {
      type: Object,
      required: true
    },
    horizontal: {
      type: Boolean,
      default: false
    },
    share: {
      type: Boolean,
      default: false
    },
    remove: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      shareProduct: false,
      snackbar: {
        show: false,
        timeout: 1500,
        text: null
      }
    }
  },
  computed: {
    amoutn () {
      return store.getters.checkProductOnCart(this.product._id)
    },

    url () {
      return this.$route.path
    },

    favorite () {
      this.product.isFavorite = store.getters.checkProductOnFavorites(this.product._id) > 0
      return this.product.isFavorite
      // return this.isFavorite()
    },

    verticalPosition () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return !!this.horizontal
        case 'sm': return !!this.horizontal
        // case 'md': return false
        // case 'lg': return false
        // case 'xl': return false
      }
      return false
    }

  },
  methods: {

    addToCart () {
      // product.amount = parseInt(product.amount) + parseInt(this.amount)
      this.$store.dispatch('addToCart', this.product)
    },

    checkOnFavorites () {
      if (!this.product.isFavorite) {
        userServices.addToWishList(this.product._id).then((result) => {
          if (result) {
            this.product.isFavorite = true
            this.$store.dispatch('addToFavoriteProducts', this.product)
          }
        }).catch((err) => {
          if (err.response.status === 401) {
            this.$toast.warning('Debe logearse para agregar a favoritos')
          } else {
            this.snackbar.show = true
            this.snackbar.text = 'No se pudo agregar a favorito'
          }
        })
      } else {
        userServices.removeToWishList(this.product._id).then((result) => {
          if (result) {
            this.product.isFavorite = false
            this.$store.dispatch('removeFromFavoriteProducts', this.product)
          }
        }).catch((err) => {
          if (err.response.status === 401) {
            this.$toast.warning('Debe logearse para eliminar de favoritos')
          } else {
            this.snackbar.show = true
            this.snackbar.text = 'No se pudo eliminar de favoritos'
          }
        })
      }
    },

    isFavorite () {
      this.product.isFavorite = store.getters.checkProductOnFavorites(this.product._id) > 0
      return this.product.isFavorite
    },

    removeFromFavorite () {
      return this.$emit('removeFromFavorite')
    },

    getImgUrl (image) {
      return process.env.VUE_APP_API_IMAGE_URL + image
    },

    goToDetails () {
      if (!this.$route.path.includes('stores') || (this.$route.path.includes('stores') && !this.$route.params.slug)) {
        if (this.product.store instanceof Object) {
          router.push({ path: '/stores/' + this.product.store.slug + '/' + this.product.category.slug, hash: this.product.slug })
        } else {
          router.push({ path: '/stores/' + this.product.store + '/' + this.product.category.slug, hash: this.product.slug })
        }
      }
    },

    // shareProduct (product) {
    //   alert('Url copiada!')
    // },

    copyURL () {
      // const Url = document.querySelector('#product_url')
      // const Url = this.$refs.product_url
      // const Url = null
      // Url.innerHTML = process.env.VUE_APP_API_URL + '/stores/608f6ee7f55ca2341c165e33/608abb08bdda2134c010713b?p=6090254f0e61953ff868c38e'
      // console.log(Url.innerHTML)
      // Url.select()
      // document.execCommand('copy')
      // this.shareProduct = false

      this.snackbar.show = true
      this.snackbar.text = 'Enlace copiado!'
    }

  }
}
</script>

<style lang="scss" scoped>

  @media (min-width: 960px) {
    #vertical-card{
      .v-card {
        max-width: 385px !important;
      }
      .v-image {
        max-width: 385px !important;
      }
    }
  }

  .v-card {
    // filter: drop-shadow(2px 1px 2px rgba(0, 0, 0, 0.161)) !important;
    // box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 0px 0px 0px rgba(0, 0, 0, 0.14), 100px 23px 100px 0px rgba(0, 0, 0, 0.12) !important;

    :hover{
      cursor: pointer;
    }

    .v-card__title button.v-btn {
      white-space: nowrap;
      text-align: center;
      font-family: Raleway;
      font-style: normal;
      font-weight: bold;
      font-size: 9px;
      color: rgba(255, 102, 0, 1);
      height: 21px;
      max-width: 100%;
      background-color: transparent;
      // box-shadow: 0px 2px 124px 9px rgba(0, 0, 0, 0.2),
      //   0px 1px 1px 0px rgba(0, 0, 0, 0.14), 1px 2px 66px 0px rgba(0, 0, 0, 0.12) !important;
      // box-shadow: 0px 2px 55px 9px rgba(0, 0, 0, 0.1), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 1px 2px 0px 0px rgba(0, 0, 0, -0.88) !important;
      box-shadow: 0px -452px 128px 0px rgba(0, 0, 0, 0.1), 0px 1px 35px 0px rgba(0, 0, 0, 0.1), 0px 0px 1px 0px rgba(0, 0, 0, 0) !important;
    }

    .v-image {
      /* padding: 10px 25px !important; */
      // background-image: url(http://localhost:8080/img/combo_mixto.7a21789b.png);
      // background-position: center;
      // background-repeat: no-repeat;
      /* background-size: contain !important; */
      border-top-right-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
    }

    .v-card__text div:first-child {
      // white-space: nowrap;
      text-align: left;
      font-family: Raleway;
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      color: rgba(112, 112, 112, 1);
      margin-bottom: 0.5rem;
    }

    .v-card__text div:nth-child(2) {
      line-height: normal;
      text-align: justify;
      font-family: Raleway;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      color: rgba(112, 112, 112, 1);
      max-height: 60px;
      overflow-y: auto;
    }

    .v-slide-group.v-item-group > .v-slide-group__next {
      position: absolute;
      height: 100% !important;
      min-height: 100% !important;
      right: 0;
      -webkit-box-shadow: 24px 0px 133px -72px rgb(255 255 255) !important;
      -moz-box-shadow: 20px 0px 120px 120px rgb(255 255 255) !important;
      box-shadow: 69px 0px 120px 120px rgb(255 255 255) !important;
      background: rgb(255, 255, 255);
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0.482533) -133%,
        rgba(255, 255, 255, 1) 80%,
        rgba(255, 255, 255, 1) 126%
      ) !important;
    }

    .v-card__actions .v-btn .v-icon{
      height: 40px !important;
      font-size: 32px !important;
      width: 40px !important;
    }

  }
  #price {
    white-space: nowrap;
    text-align: left;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    color: rgba(48, 153, 153, 1);
  }
  .col {
    padding: 0 12px;
  }

#horizontal-card{
  .v-card{
    font-family: 'Raleway-bold';
    color: rgba(112,112,112,1) !important;

    // p{
    //   margin-bottom: 0 !important;
    // }

    #product-info{
      text-align: justify;
      font-family: Raleway;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      color: rgba(112,112,112,1);
      overflow-y: auto;
      max-height: 73%;
    }

    #title{
      font-size: 20px !important;
      line-height: normal;
      text-align: left;
      font-family: 'Raleway-bold' !important;
      color: rgba(112,112,112,1) !important;
    }

    #airline{
      font-family: 'Raleway';
      color: rgba(112,112,112,1) !important;
    }

    #img-col{
      background: rgba(217,239,239,1);
      height: 260;
      width: inherit;
      border-top-left-radius: inherit;
      border-bottom-left-radius: inherit;
      border-top-right-radius: unset;

      .v-image{
        margin: 2.5rem 0 !important;
      }
    }

    .v-card__actions .col{
      padding: 0 12px !important;
    }

    .v-divider{
      margin: 9px 0;
    }

    .row{
      margin: 0;
    }
  }

  .col {
    padding: 12px;
  }
}
</style>
